<template>
  <div class="collect">
    <van-nav-bar title="收藏的文章" left-text="返回" left-arrow @click-left="$router.back()" />

    <div class="nav" v-if="collectList.length>0">
      <span v-if="!isEdit" @click="isEdit = true">管 理</span>
      <span v-else @click="onCancle">取 消</span>
    </div>

    <div class="list">
      <van-checkbox-group v-model="selectList" ref="checkboxGroup" @change="change">
        <div class="item" v-for="(item, index) in collectList" :key="index">
          <div class="mask" v-if="isEdit">
            <van-checkbox :name="item.id" />
          </div>
          <studyCard :article="item" @click.native="toArticle(item)"></studyCard>
        </div>
      </van-checkbox-group>
    </div>

    <van-empty v-if="collectList.length==0" class="custom-image" :image="empty" description="您还未收藏任何文章~">
      <div class="button" @click="$router.push('/study')">前去学习</div>
    </van-empty>

    <van-submit-bar v-if="isEdit" button-text="删除" @submit="deleteAll" style="z-index:999;">
      <van-checkbox v-model="checked" @click="$refs.checkboxGroup.toggleAll(checked)">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import studyCard from '@/components/studyCard'
export default {
  components: {
    studyCard
  },
  data() {
    return ({
      empty: require('../assets/img/empty.png'),
      collectList: JSON.parse(localStorage.getItem('collectList')) || [],
      selectList: [],
      isEdit: false,
      checked:false,
    })
  },
  methods: {
    deleteAll(){
      if(this.selectList.length==0){
        this.$toast.fail('您还未选择文章~')
        return
      }
      this.$dialog.confirm({
        title:'提示',
      message: '是否确认删除',
    }).then(()=>{
      this.selectList.forEach(item=>{
        this.collectList.forEach((iitem,iindex)=>{
          if(iitem.id == item) this.collectList.splice(iindex,1)
        })
      })
      localStorage.setItem('collectList',JSON.stringify(this.collectList))
      this.isEdit = false
      this.$toast.success('删除成功')
    }).catch(()=>{
      this.checked = false
      this.isEdit = false
      this.$toast.success('已取消')
    })
      
    },
    change() {
      console.log(this.selectList);
      if(this.selectList.length == this.collectList.length) this.checked = true
      else this.checked = false
    },
    onCancle() {
      this.isEdit = false
    },
    toArticle(article) {
      this.$router.push({
        name: 'article',
        params: {
          article: JSON.stringify(article)
        }
      })
    }
  }
}
</script>

<style scoped>
::v-deep .van-checkbox__icon--checked .van-icon {
    color: #fff;
    background-color: #ee0a24;
    border-color: #ee0a24;
}
.button {
  background: rgb(154, 75, 75);
  color: #fff;
  width: 120px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  margin: 20px auto;
}
.collect{
  background: rgb(248, 242, 242);
  min-height:100vh;
}
.van-checkbox {
  position: absolute;
  left: 10px;
  top: 10px;
}

.mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.item {
  position: relative;
}

.list {
  padding: 0 20px;
}

.nav {
  position: relative;
  height: 30px;
  margin: 20px 20px 10px 0;
}

.nav span {
  display: block;
  position: absolute;
  right: 0;
  width: 100px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  color: #fff;
  background: rgb(154, 75, 75);
  border-radius: 5px;
}
</style>